h1, h2, h3, h4, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.hidden {
  display: none;
}

.container {
  max-width: 1400px;
  margin: auto;
  padding: 0 15px;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding-top: 30px;
  padding-bottom: 30px;
  display: flex;
}

.menu {
  text-transform: uppercase;
  gap: 40px;
  margin: 0;
  list-style: none;
  display: flex;
}

.menu-item {
  font-size: 16px;
  position: relative;
}

.menu-item:not(:last-child):after {
  content: "";
  width: 2px;
  height: 130%;
  background: rgba(164, 158, 158, .4);
  display: block;
  position: absolute;
  top: -20%;
  right: -20px;
  transform: rotate(35deg)translateX(-10%);
}

.main {
  box-sizing: border-box;
  padding-top: 40px;
}

.hero_section {
  justify-content: space-between;
  gap: 30px;
  display: flex;
}

.hero_main {
  max-width: 100%;
  height: 100vh;
  border-radius: 8px;
  flex: 0 80%;
  overflow: hidden;
}

.hero_main-block {
  width: 100%;
  height: 65vh;
  background: url("bg.d99acea5.jpg") center / cover no-repeat;
  position: relative;
}

.hero-title {
  width: 350px;
  text-transform: uppercase;
  word-spacing: 15px;
  letter-spacing: 1.2px;
  color: #6f6868;
  font-size: 40px;
  line-height: 1.2;
  position: absolute;
  top: 40%;
  left: 5%;
}

.hero-title span {
  color: #fff;
}

.cat-info-container {
  background: #e6e0e0;
  border-radius: 8px;
  align-items: center;
  display: flex;
}

.cat-text {
  flex: 0 50%;
  padding: 0 20px;
  font-size: 20px;
}

.cat-text h2 {
  text-align: center;
  margin-bottom: 20px;
  font-size: 56px;
}

.cat-text span {
  font-weight: bolder;
}

.cat-text p:not(:last-child) {
  margin-bottom: 20px;
}

.cat-image {
  border-radius: 8px;
  flex: 0 50%;
  padding: 0 0 50%;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 18px rgba(88, 126, 236, .18);
}

.cat-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
}

.hero_search-block p {
  color: #6f6868;
  margin-bottom: 10px;
  font-size: 26px;
}

.breed-select {
  width: 100%;
  height: 24px;
  margin-bottom: 15px;
  font-size: 16px;
}

.loader {
  width: 10px;
  height: 10px;
  color: #ba7e6c;
  border-radius: 50%;
  animation: fill 1s ease-in infinite alternate;
  position: relative;
  top: 0;
  left: 40%;
}

.loader:before, .loader:after {
  content: "";
  height: 100%;
  width: 100%;
  border-radius: 50%;
  animation: fill .9s ease-in infinite alternate;
  position: absolute;
  top: 0;
  left: 28px;
}

.loader:after {
  animation-duration: 1.1s;
  left: auto;
  right: 28px;
}

@keyframes fill {
  0% {
    box-shadow: inset 0 0 0 2px;
  }

  100% {
    box-shadow: inset 0 0 0 10px;
  }
}

